import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  HostBinding,
  Input,
  OnInit,
  Output,
  Renderer2,
} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ApiService } from 'src/app/shared/api.service';
import { NotificationService } from 'src/app/shared/notification.service';
import {
  _modifierSummary,
  _total,
} from '../../ordernow/product/product.component';

const onScrollend = callback => {
  // Setup scrolling variable
  let scrolling;
  // Listen for scroll events
  const scrollListener = () => {
    // Clear our timeout throughout the scroll
    window.clearTimeout(scrolling);

    // Set a timeout to run after scrolling ends
    scrolling = setTimeout(() => {
      window.removeEventListener('scroll', scrollListener);
      // Run the callback
      callback();
    }, 1000);
  };
  window.addEventListener('scroll', scrollListener);
  scrollListener();
};

@Component({
  selector: 'app-cartitem',
  templateUrl: './cartitem.component.html',
  styleUrls: ['./cartitem.component.css'],
})
export class CartitemComponent implements OnInit, AfterViewInit {
  @HostBinding('class') classes = 'animated';

  @Input()
  item;

  @Output()
  itemChange = new EventEmitter();

  product;

  constructor(
    private api: ApiService,
    private notificationService: NotificationService,
    private activatedroute: ActivatedRoute,
    private thiselement: ElementRef,
    private renderer: Renderer2
  ) {}

  async ngOnInit() {
    this.product = await this.api
      .get(`/api1/products/${this.item.product.id}`)
      .toPromise();
  }

  async ngAfterViewInit() {
    if (this.activatedroute.snapshot.queryParams.cartitemid === this.item.id) {
      await new Promise(r => setTimeout(r, 500));
      this.thiselement.nativeElement.scrollIntoView({
        behavior: 'smooth',
        block: 'start',
      });
      await new Promise(r => setTimeout(r, 1000));
      this.renderer.removeClass(this.thiselement.nativeElement, 'flash');
      // void this.thiselement.nativeElement.offsetWidth; // tslint:disable-line
      setTimeout(() => {
        this.renderer.addClass(this.thiselement.nativeElement, 'flash');
      }, 1);
      // onScrollend(() => {
      //   });
    }
  }

  onQuantityChanged(event) {
    this.item.total =
      _total(this.item.product, this.item.modifiers) * this.item.quantity;
    this.itemChange.emit(this.item);
  }

  onMgsvalueChange({ mgsvalue, mgsvalid }) {
    this.item.modifiers = mgsvalue;
    this.item.modifier = _modifierSummary(mgsvalue);
    this.itemChange.emit(this.item);
  }

  total(item) {
    // console.log(JSON.stringify(item, null, 2));
    return _total(item.product, item.modifiers) * item.quantity;
  }

  // remove(item) {
  //   this.api.delete(`/api1/cartitems/${item.id}`).toPromise();
  // }
}
