import { Location } from '@angular/common';
import {
  Component,
  ElementRef,
  EventEmitter,
  OnInit,
  Output,
  QueryList,
  Renderer2,
  ViewChildren,
} from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiService } from 'src/app/shared/api.service';
import { NotificationService } from 'src/app/shared/notification.service';

const _viewCart = async (api, pageid, fbid) => {
  await api
    .post(`/api1/fbcommand`, {
      pageid: pageid,
      fbid: fbid,
      commands: [
        {
          type: 'VIEW_CART',
        },
      ],
    })
    .toPromise();
};

@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.css'],
})
export class ProductsComponent implements OnInit {
  pageid;
  fbid;

  cart$;

  quantity2 = 0;
  total2 = 0;

  // @ViewChild('badge')
  // badge: ElementRef;

  @ViewChildren('badge')
  badges: QueryList<ElementRef>;

  products = [];

  @Output()
  itemChange = new EventEmitter();

  constructor(
    private api: ApiService,
    private notificationService: NotificationService,
    private activatedroute: ActivatedRoute,
    private router: Router,
    private location: Location,
    private renderer: Renderer2
  ) {}

  async ngOnInit() {
    this.pageid = this.activatedroute.snapshot.queryParams.pageid;
    this.fbid = this.activatedroute.snapshot.queryParams.fbid;
    this.cart$ = this.api.get(`/api1/cartitems/${this.pageid}/${this.fbid}`);

    const categoryid = this.activatedroute.snapshot.queryParams.categoryid;
    const productid = this.activatedroute.snapshot.queryParams.productid;
    if (categoryid) {
      const category = (await this.api
        .get(`/api1/categories/${categoryid}`)
        .toPromise()) as any;
      this.products = category.products.filter(product => product.active);
    } else if (productid) {
      this.products = [
        await this.api.get(`/api1/products/${productid}`).toPromise(),
      ];
    } else {
      this.products = (await this.api
        .get(`/api1/products`)
        .toPromise()) as any[];
    }

    // _loadFacebook();
  }

  quantity(cart) {
    return (
      cart.map(cartitem => cartitem.quantity).reduce((a, b) => a + b, 0) +
      this.quantity2
    );
  }

  total(cart) {
    return cart.map(i => i.total).reduce((a, b) => a + b, 0) + this.total2;
  }

  async onItemChanged(item) {
    this.animateBadge();
    this.quantity2 += item.quantity;
    this.total2 += item.total;

    await this.api.post(`/api1/cartitems`, item).toPromise();

    // setTimeout(() => {
    // });
    // this.router.navigate(['cart'], {
    //   queryParams: this.queryParams,
    //   relativeTo: this.activatedroute,
    // });

    await _viewCart(this.api, this.pageid, this.fbid);
  }

  async closeWindow() {
    // _fbCloseWindow();
    // await _viewCart(this.api, this.pageid, this.fbid);
    location.href = '/fbclose.html';
  }

  animateBadge() {
    this.badges.forEach(badge => {
      this.renderer.removeClass(badge.nativeElement, 'bounce');
      // void badge.nativeElement.offsetWidth; // tslint:disable-line
      setTimeout(() => {
        this.renderer.addClass(badge.nativeElement, 'bounce');
      }, 1);
    });
  }
}
