import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RouteModule } from 'src/app/route/route.module';
import { RouteComponent } from 'src/app/route/route/route.component';
import { CartComponent } from './cart/cart/cart.component';
import { ProductdetailsComponent } from './ordernow/productdetails/productdetails.component';
import { ProductsComponent } from './ordernow/products/products.component';

const routes: Routes = [
  {
    path: 'products',
    component: ProductsComponent,
  },
  {
    path: 'cart',
    component: CartComponent,
  },
  {
    path: 'product/:productid',
    component: RouteComponent,
    data: { wrappedComponent: ProductdetailsComponent },
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes), RouteModule],
  exports: [RouterModule],
  entryComponents: [ProductdetailsComponent],
})
export class OrdernowRoutingModule {}
