import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'asCurrency',
  pure: true,
})
export class CurrencyPipe implements PipeTransform {
  transform(value: any, args?: any): any {
    return `RM ${(value / 100).toFixed(2)} MYR`;
    // return `SGD$${(value / 100).toFixed(2)}`;
    // return null;
  }
}
