import {
  Component,
  ComponentFactoryResolver,
  OnDestroy,
  OnInit,
  ViewChild,
  ViewContainerRef,
} from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';

@Component({
  selector: 'app-route',
  templateUrl: 'route.component.html',
})
export class RouteComponent implements OnInit, OnDestroy {
  @ViewChild('container', { read: ViewContainerRef })
  viewContainerRef: ViewContainerRef;

  componentRef;

  constructor(
    // private router: Router,
    // private viewContainerRef: ViewContainerRef,
    private activatedroute: ActivatedRoute,
    private resolver: ComponentFactoryResolver
  ) {}

  ngOnInit() {
    this.componentRef = this.createComponent(
      this.activatedroute.snapshot.data.wrappedComponent
    );

    this.activatedroute.paramMap.subscribe((params: ParamMap) => {
      this.injectRouteParamsIntoComponent(this.componentRef.instance);
    });

    // this.injectRouteParamsIntoComponent(this.componentRef.instance);
    // this.route.data.subscribe((data: any) => {
    // const instance = this.createComponentRef(data.wrappedComponent);
    // this.injectRouteParamsIntoComponent(instance);
    //   });
  }

  ngOnDestroy() {
    this.componentRef.destroy();
  }

  createComponent(type) {
    this.viewContainerRef.clear();
    const factory = this.resolver.resolveComponentFactory(
      this.activatedroute.snapshot.data.wrappedComponent
    );
    return this.viewContainerRef.createComponent(factory);
  }

  injectRouteParamsIntoComponent(component) {
    this.activatedroute.paramMap.subscribe((map: ParamMap) => {
      for (const key of map.keys) {
        component[key] = map.get(key);
      }
      if (component.ngOnInit) {
        component.ngOnInit();
      }
      // map.keys.forEach(key => {
      //   component[key] = map.get(key);
      // });
    });
  }
}
