import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'app-modifiergroups',
  templateUrl: './modifiergroups.component.html',
  styleUrls: ['./modifiergroups.component.css'],
})
export class ModifiergroupsComponent implements OnInit {
  @Input()
  modifiergroups;

  @Input()
  mgsvalue;

  @Output()
  mgsvalueChange = new EventEmitter();

  mgvalid = {};

  constructor() {}

  ngOnInit() {
    this.mgsvalue = this.mgsvalue || {};
    this.modifiergroups = this.modifiergroups || [];
    for (const modifiergroup of this.modifiergroups) {
      if (modifiergroup.min) {
        this.mgvalid[modifiergroup.name] = false;
      }
    }
  }

  onMgvalueChange(name, { mgvalue, mgvalid }) {
    this.mgsvalue[name] = mgvalue;
    this.mgvalid[name] = mgvalid;

    const event = {
      mgsvalue: this.mgsvalue,
      mgsvalid: Object.values(this.mgvalid).filter(v => !v).length === 0,
    };

    this.mgsvalueChange.emit(event);
  }
}
