import {
  Component,
  ElementRef,
  OnInit,
  Renderer2,
  ViewChild,
} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ApiService } from 'src/app/shared/api.service';
import { NotificationService } from 'src/app/shared/notification.service';
import { _total } from '../../ordernow/product/product.component';

const _viewCart = async (api, pageid, fbid) => {
  await api
    .post(`/api1/fbcommand`, {
      pageid: pageid,
      fbid: fbid,
      commands: [
        {
          type: 'VIEW_CART',
        },
      ],
    })
    .toPromise();
};

@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.css'],
})
export class CartComponent implements OnInit {
  pageid;
  fbid;

  cartitemid;
  cartitems = [];

  // outlet;

  @ViewChild('badge')
  badge: ElementRef;

  constructor(
    private api: ApiService,
    private notificationService: NotificationService,
    private activatedroute: ActivatedRoute,
    private renderer: Renderer2
  ) {}

  async ngOnInit() {
    this.pageid = this.activatedroute.snapshot.queryParams.pageid;
    this.fbid = this.activatedroute.snapshot.queryParams.fbid;
    this.cartitemid = this.activatedroute.snapshot.queryParams.cartitemid;
    // if (this.cartitemid) {
    //   this.cartitems = [
    //     await this.api.get(`/api1/cartitems/${this.cartitemid}`).toPromise(),
    //   ];
    // } else {
    //   this.cartitems = (await this.api
    //     .get(`/api1/cartitems/${this.pageid}/${this.fbid}`)
    //     .toPromise()) as any;
    // }
    this.cartitems = <any[]>(
      await this.api
        .get(`/api1/cartitems/${this.pageid}/${this.fbid}`)
        .toPromise()
    );
  }

  async onItemChanged(item) {
    item.total = _total(item.product, item.modifiers) * item.quantity;
    await this.api.post(`/api1/cartitems`, item).toPromise();

    await _viewCart(this.api, this.pageid, this.fbid);

    this.renderer.removeClass(this.badge.nativeElement, 'bounce');
    // void this.badge.nativeElement.offsetWidth; // tslint:disable-line
    setTimeout(() => {
      this.renderer.addClass(this.badge.nativeElement, 'bounce');
    }, 1);
  }

  quantity(cartitems) {
    return cartitems
      .map(cartitem => cartitem.quantity)
      .reduce((a, b) => a + b, 0);
  }

  total(cartitems) {
    return cartitems.map(cartitem => cartitem.total).reduce((a, b) => a + b, 0);
  }

  async closeWindow() {
    // await _viewCart(this.api, this.pageid, this.fbid);
    location.href = '/fbclose.html';
  }
}
