import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'app-modifiergroup',
  templateUrl: './modifiergroup.component.html',
  styleUrls: ['./modifiergroup.component.css'],
})
export class ModifiergroupComponent implements OnInit {
  @Input()
  modifiergroup;

  @Input()
  mgvalue;

  @Output()
  mgvalueChange = new EventEmitter();

  selectedcount = 0;

  message: string;
  valid = false;

  constructor() {}

  ngOnInit() {
    this.mgvalue = this.mgvalue || {};
    this.selectedcount = Object.keys(this.mgvalue).length;
    this.message = this._message();
    this.valid = this._valid();
  }

  onMvalueChange(name, mvalue) {
    // console.log(JSON.stringify(mvalue, null, 2));

    if (mvalue.selected) {
      // selecting new option
      this.mgvalue[name] = mvalue;
      if (this.modifiergroup.max) {
        const keys = Object.keys(this.mgvalue);
        if (keys.length > this.modifiergroup.max) {
          delete this.mgvalue[keys[0]];
        }
      }
    } else {
      // unselecting option
      delete this.mgvalue[name];
      if (this.modifiergroup.min) {
        const keys = Object.keys(this.mgvalue);
        if (keys.length < this.modifiergroup.min) {
          this.mgvalue[name] = { ...mvalue, selected: true };
        }
      }
    }

    this.message = this._message();
    this.valid = this._valid();
    this.mgvalueChange.emit({ mgvalue: this.mgvalue, mgvalid: this.valid });
  }

  _message() {
    const keys = Object.keys(this.mgvalue);
    if (
      this.modifiergroup.min &&
      this.modifiergroup.max &&
      this.modifiergroup.min === this.modifiergroup.max
    ) {
      if (this.modifiergroup.min > 1) {
        return `Select ${this.modifiergroup.min} options.`;
      } else {
        return `Select ${this.modifiergroup.min} option.`;
      }
    }
    if (this.modifiergroup.min && keys.length < this.modifiergroup.min) {
      if (this.modifiergroup.min > 1) {
        return `Select more than ${this.modifiergroup.min} options.`;
      } else {
        return `Select more than ${this.modifiergroup.min} option.`;
      }
    }
    if (this.modifiergroup.max) {
      if (this.modifiergroup.max > 1) {
        return `Select up to ${this.modifiergroup.max} options.`;
      } else {
        return `Select up to ${this.modifiergroup.max} option.`;
      }
    }
    return undefined;
  }

  _valid() {
    const keys = Object.keys(this.mgvalue);
    if (this.modifiergroup.min && keys.length < this.modifiergroup.min) {
      return false;
    }
    if (this.modifiergroup.max && keys.length > this.modifiergroup.max) {
      return false;
    }
    return true;
  }
}
