import { Location, ViewportScroller } from '@angular/common';
import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnInit,
  Output,
  QueryList,
  Renderer2,
  ViewChildren,
} from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiService } from 'src/app/shared/api.service';
import { NotificationService } from 'src/app/shared/notification.service';

export const _total = (product, modifiers) => {
  modifiers = modifiers || {};
  const modifiertotal = Object.values(modifiers)
    .map(mg =>
      Object.values(mg)
        .map(m => m.price)
        .reduce((a, b) => a + b, 0)
    )
    .reduce((a, b) => a + b, 0);
  return product.price + modifiertotal;
};

export const _log = o => {
  console.log('---');
  console.log(JSON.stringify(o, null, 2));
};

export const _modifierSummary = modifiers => {
  const summary = Object.values(modifiers)
    .map(m => Object.keys(m).join(', '))
    .filter(a => a.length > 0)
    .join(', ');
  return summary ? `(${summary})` : '';
};

@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.css'],
})
export class ProductComponent implements OnInit, AfterViewInit {
  @Input()
  productid;
  product;

  pageid;
  fbid;

  modifiers = {};
  modifiersvalid = false;

  showmodifiers = false;
  quantity = 0;

  @Output()
  itemChange = new EventEmitter();

  @ViewChildren('button')
  buttons: QueryList<ElementRef>;

  @ViewChildren('badge')
  badges: QueryList<ElementRef>;

  addedtocard = 0;

  constructor(
    private api: ApiService,
    private notificationService: NotificationService,
    private activatedroute: ActivatedRoute,
    private router: Router,
    private location: Location,
    private thiselement: ElementRef,
    private renderer: Renderer2,
    private viewportScroller: ViewportScroller
  ) {
    this.pageid = this.activatedroute.snapshot.queryParams.pageid;
    this.fbid = this.activatedroute.snapshot.queryParams.fbid;

    // this.fragment = this.activatedroute.snapshot.fragment;

    // this.activatedroute.queryParams.subscribe(params => {
    //   this.pageid = params['pageid'];
    //   this.fbid = params['fbid'];
    //   this.scrollproductid = params['productid'];
    // });
  }

  async ngOnInit() {
    this.product = await this.api
      .get(`/api1/products/${this.productid}`)
      .toPromise();
    // .pipe(
    //   tap(() => {
    //     this.thiselement.nativeElement.scrollIntoView({ behavior: "smooth", block: "start" });
    //   })
    // );
  }

  async ngAfterViewInit() {
    // if (this.scrollproductid === this.productid) {
    //   this.thiselement.nativeElement.scrollIntoView();
    // }

    if (this.activatedroute.snapshot.queryParams.productid === this.productid) {
      await new Promise(r => setTimeout(r, 500));
      // const y = this.thiselement.nativeElement.offsetTop;
      // const { x, y } = this.thiselement.nativeElement.getBoundingClientRect();
      // this.viewportScroller.scrollToPosition([x, y]);
      this.thiselement.nativeElement.scrollIntoView({
        behavior: 'smooth',
        block: 'start',
      });
      await new Promise(r => setTimeout(r, 1000));
      this.renderer.removeClass(this.thiselement.nativeElement, 'flash');
      // void this.thiselement.nativeElement.offsetWidth; // tslint:disable-line
      setTimeout(() => {
        this.renderer.addClass(this.thiselement.nativeElement, 'flash');
      }, 1);

      // onScrollend(() => {
      //   });
    }
  }

  async addToCart(product, modifiers, quantity) {
    this.addedtocard++;
    this.animateBadge();

    const cartitem = {
      pageid: this.pageid,
      fbid: this.fbid,
      product,
      quantity,
      modifiers,
      modifier: _modifierSummary(modifiers),
      total: _total(product, modifiers),
    };

    this.itemChange.emit(cartitem);

    await this.api
      .post(`/api1/fbcommand`, {
        pageid: this.pageid,
        fbid: this.fbid,
        commands: [
          {
            type: 'TEXT',
            payload: `${cartitem.quantity} x "${cartitem.product.name}${
              cartitem.modifier
            }" has been added to cart.`,
          },
        ],
      })
      .toPromise();

    // await this.api.post(`/api1/cartitems`, cartitem).toPromise();
  }

  total(product, modifiers) {
    return _total(product, modifiers);
  }

  modifierSummary(modifiers) {
    return _modifierSummary(modifiers);
  }

  onMgsvalueChange({ mgsvalue, mgsvalid }) {
    this.animateButtons();
    this.modifiers = mgsvalue;
    this.modifiersvalid = mgsvalid;
  }

  onQuantityChanged(event) {
    this.animateButtons();
    this.quantity = event;
  }

  animateButtons() {
    this.buttons.forEach(button => {
      this.renderer.removeClass(button.nativeElement, 'flash');
      // void button.nativeElement.offsetWidth; // tslint:disable-line
      setTimeout(() => {
        this.renderer.addClass(button.nativeElement, 'flash');
      }, 1);
    });
  }

  animateBadge() {
    this.badges.forEach(badge => {
      this.renderer.removeClass(badge.nativeElement, 'bounce');
      // void badge.nativeElement.offsetWidth; // tslint:disable-line
      setTimeout(() => {
        this.renderer.addClass(badge.nativeElement, 'bounce');
      }, 1);
    });
  }

  canaddtocart(product, modifiers) {
    if (this.quantity <= 0) {
      return false;
    }
    return this.modifiersvalid;
  }
}
